import React, { useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Shield, Lock, Send, Smartphone, User } from 'lucide-react';
import { cn } from "@/lib/utils";

type Props = {
  open: boolean;
  onClose: () => void;
  onSubmit: (deviceId: string, pin: string) => void;
  deviceId: string | null;
  recipientMsisdn?: string;
  recipientName?: string;
  helperMessage?: string;
};

export default function PinModal({ open, onClose, onSubmit, deviceId, recipientMsisdn, recipientName, helperMessage }: Props) {
  const [pin, setPin] = useState('');

  const handleSubmit = () => {
    onSubmit(deviceId || '', pin);
    setPin('');
  };

  return (
    <Dialog open={open} onOpenChange={(val) => !val && onClose()}>
      <DialogContent className="sm:max-w-md border-t-4 border-t-red-600 shadow-2xl">
        <DialogHeader className="items-center space-y-2 pb-2">
          <div className="h-12 w-12 rounded-full bg-red-100 flex items-center justify-center mb-2">
            <Lock className="h-6 w-6 text-red-600" />
          </div>
          <DialogTitle className="text-xl font-bold text-center text-gray-900">
              
          </DialogTitle>
          <p className="text-sm text-center text-gray-500">
                
          </p>
        </DialogHeader>

        <div className="space-y-6 py-4">
          {/* Recipient Info Card */}
          <div className="bg-gray-50 p-4 rounded-xl border border-gray-100 space-y-3">
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-500 flex items-center gap-1.5">
                <Smartphone className="w-4 h-4" />
                 
              </span>
              <span className="font-mono font-bold text-lg text-gray-900" dir="ltr">
                {recipientMsisdn || '---'}
              </span>
            </div>
            
            {recipientName && (
              <div className="flex items-center justify-between pt-2 border-t border-gray-200">
                <span className="text-sm text-gray-500 flex items-center gap-1.5">
                  <User className="w-4 h-4" />
                   
                </span>
                <span className="font-bold text-base text-emerald-700">
                  {recipientName}
                </span>
              </div>
            )}
          </div>

          {/* Helper Message */}
          {helperMessage && (
            <div className="text-center p-2 bg-yellow-50 text-yellow-700 text-sm rounded-lg border border-yellow-100">
              {helperMessage}
            </div>
          )}

          {/* PIN Input */}
          <div className="space-y-2">
            <Label htmlFor="pin-input" className="sr-only"> </Label>
            <div className="relative">
              <Input
                id="pin-input"
                type="password"
                value={pin}
                onChange={(e) => setPin(e.target.value)}
                onKeyDown={(e) => {
                  if (e.key === 'Enter') handleSubmit();
                }}
                className="text-center text-2xl font-bold tracking-[0.5em] h-14 border-2 focus-visible:ring-red-500 focus-visible:border-red-500 transition-all"
                placeholder=""
                autoFocus
                autoComplete="off"
              />
              <Shield className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-300 w-5 h-5" />
            </div>
          </div>
        </div>

        {/* Actions */}
        <div className="flex gap-3 mt-2">
          <Button 
            variant="outline" 
            onClick={onClose}
            className="flex-1 h-11 text-gray-700 hover:bg-gray-50 border-gray-200"
          >
            
          </Button>
          <Button 
            onClick={handleSubmit}
            className="flex-1 h-11 bg-red-600 hover:bg-red-700 text-white shadow-md shadow-red-200 transition-all"
            disabled={!pin}
          >
            <Send className="w-4 h-4 ml-2" />
             
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}
